*{
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header{
  width: 100%;
  height: 70px;
  background-color: rgb(80, 77, 77);
  position: fixed;
}
nav{
  width: 100%;
  height: 100%;
  position: relative;
}
.menu{
  height: 100%;
  display: flex;
  align-items: center;
  position: relative;
  &__item{
      list-style-type: none;
      color: rgb(11, 155, 107);
      text-transform: uppercase;
      text-shadow: 2px 2px 4px black;
      position: relative;
      font-size: 24px;
  }
  &__link{
      display: flex;
      justify-content: center;
      align-items: center;
  }
  &__item:nth-child(1){
	margin-left: 40px;
}
  &__item:nth-child(2){
      margin-left: 20px;
  }

}
footer{
  height: 50px;
  width: 100%;
  background-color: rgb(80, 77, 77);
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: rgb(11, 155, 107);
  text-transform: uppercase;
  text-shadow: 2px 2px 4px black
}
footer span{
  margin-right: 40px;
}
.content{
	width: 100%;
	min-height: calc(100vh - 70px - 50px);
}
.btn{
  width: 90px;
  height: 25px;
  border-radius: 8px;
  border: none;
  background-color: rgb(11, 155, 107);
  text-transform: uppercase;
  color: rgb(218, 218, 91);
  cursor: pointer;
}
.btn:hover{
  filter: opacity(0.8);
  transform: scale(1.1, 1.1);
}
.card{
  width: 100%;
  height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px solid rgb(80, 77, 77);
  border-radius: 8px;
  background-color: rgb(11, 155, 107);
  box-shadow: 6px 4px 4px rgb(80, 77, 77);
  &__img{
    width: 100%;
    height: 220px;
    margin-top: 15px;
  }
  &__img img{
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
  &__descr{
    height: 100px;
    width: 100%;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(80, 77, 77);
    color: rgb(11, 155, 107);
    text-shadow: 2px 2px 4px black;
    font-size: 20px;
    text-transform: lowercase;
  }
  &__price{
    height: 80px;
    width: 100%;
    padding: 0 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(218, 218, 91);
    border-radius: 0 0 8px 8px;
  }
  &__price span{
    color: rgb(80, 77, 77);
    font-weight: bold;
  }
}
.card h4{
  text-align: center;
  font-weight: bold;
  color: rgb(80, 77, 77);
}
.loader{
  width: 200px;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 8px;
  background-color: green;
  color: black;
}
.wrapper{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px,1fr));
  gap: 20px;
}
.content{
  width: 100%;
  padding: 100px 40px 40px 40px;
  background-color: rgb(122, 121, 121);
  margin: 0 auto;
}
.cart{
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 50px;
  cursor: pointer;
}
.cart span{
  color: rgb(11, 155, 107);
  text-transform: uppercase;
  text-shadow: 2px 2px 4px black;
  font-size: 24px;
}
.cart_list{
  width: 500px;
  min-height: 160px;
  border: 1px solid rgb(218, 218, 91);
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  &__header{
    width: 100%;
    height: 50px;
    background-color: rgb(80, 77, 77);
    border-radius: 8px 8px 0 0;
    padding: 5px 10px;
    position: relative;
  }
  &__header img{
    height: 40px;
    width: 40px;
  }
  #close_icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
    cursor: pointer;
    &:hover{
      filter: opacity(0.8);
    }
  }
  &__footer{
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: rgb(80, 77, 77);
    padding: 10px 40px;
    color: rgb(11, 155, 107);
    text-shadow: 2px 2px 4px black;
    position: absolute;
    left: 0;
    bottom: 0;
    border-radius: 0 0 8px 8px;
    margin-top: 50px;
  }
  &__empty{
    width: 100%;
    height: 60px;
    background-color: rgb(218, 218, 91);
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(80, 77, 77);
    font-weight: bold;
    font-size: 24px;
  }
  &__order{
    width: 100%;
    padding: 0 0 50px 0;
  }
}
.cart_item{
  position: relative;
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color:  rgb(218, 218, 91);
  color: rgb(80, 77, 77);
  padding-left: 10px;
  &__img{
    width: 25px;
    height: 25px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 10px;
    cursor: pointer;
  }
}
.cart_item h4{
  margin-right: 10px;
}
.cart_item span{
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 70%;
  color: rgb(11, 155, 107);
  font-weight: bold;
}
.cart_item p{
  margin-left: 5px;
  margin-right: 5px;
  font-weight: bold;
}
.cart_item button{
  width: 25px;
  height: 20px;
  color: rgb(11, 155, 107);
  font-weight: bold;
  font-size: 14px;
  background-color: rgb(80, 77, 77);
  border: none;
  border-radius: 4px;
  cursor: pointer;
}
.buy_window{
  position: fixed;
  top: 10px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
  width: 400px;
  height: 50px;
  border: 1px solid rgb(11, 155, 107);
  background-color: rgb(11, 155, 107);
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  animation-name: showWindow;
  animation-duration: 2s;
}
.buy_window h2{
  color: rgb(80, 77, 77);
  margin-right: 10px;
}
.buy_window span{
  font-size: 22px;
  color: rgb(218, 218, 91);
}
@keyframes showWindow {
  from{
    top: -50px
  }
  to{
    top: 10px
  }
}
